.profile {
  margin: auto;
  width: 600px;
  background-color: rgb(223, 223, 223);
  padding: 80px 0;
}

.description {
  margin: auto;
  padding: 30px 0;
  width: 400px;
  background-color: white;
  text-align: center;
}

.avatar {
  width: 200px;
  border-radius: 50%;
}

.name {
  margin-top: 30px;
  margin-bottom: 20px;
  font-weight: bold;
}

.tag {
  margin-bottom: 20px;
  color: rgb(156, 154, 154);
}

.location {
  margin: 0;
  color: rgb(156, 154, 154);
}

.stats {
  display: flex;
  width: 400px;
  height: 60px;
  margin: auto;
  padding: 0;
  list-style: none;
  background-color: rgb(202, 201, 202);
}

.item {
  width: 133px;
  height: 60px;
  padding-top: 13px;
  text-align: center;

  border-right: 2px solid black;
}

.item:last-child {
  border-right: none;
}

.label {
  display: block;
}
